import Base from './base.js'
export default class Message extends Base {
  schema = {
    from: {
      //ref to collection user.username
      type: String,
      require: true
    },
    to: {
      //ref to collection user.username
      type: String,
      require: true
    },
    content: {
      type: String,
      require: true,
      default: ''
    },
    //已读？1:0
    read: {
      type: Number,
      require: true,
      default: 0
    },
    createAt: {
      type: Date,
      required: true,
      default: ()=>new Date()
    },
    updateAt: {
      type: Date,
      required: true,
      default: ()=>new Date()
    }
  }
}